<template>
  <van-tabbar route
              v-model="active">
    <van-tabbar-item v-for="(item, index) in tabBars"
                     :key="index"
                     :to="item.path"
                     :replace="true">
      <span>{{item.title}}</span>
      <img slot="icon"
           slot-scope="props"
           :src="props.active ? item.icon.active : item.icon.inactive">
    </van-tabbar-item>
  </van-tabbar>
</template>

<script>
export default {
  props: ['items'],
  data() {
    return {
      active: 0,
      tabBars: this.items,
    }
  },
}
</script>

